:root {
	--color-primary-50: #e9f4ff;
	--color-primary-100: #d7ebff;
	--color-primary-200: #b7d9ff;
	--color-primary-300: #8cbeff;
	--color-primary-400: #5e96ff;
	--color-primary-500: #3a6dff;
	--color-primary-600: #1840ff;
	--color-primary-700: #0e33f4;
	--color-primary-800: #0f2fc4;
	--color-primary-900: #163099;
	--color-primary-950: #0a1444;

	--color-neutral-00: #ffffff;
	--color-neutral-25: #fdfdfd;
	--color-neutral-50: #fafafa;
	--color-neutral-100: #f5f5f5;
	--color-neutral-200: #e9eaeb;
	--color-neutral-300: #d5d7da;
	--color-neutral-400: #a4a7ae;
	--color-neutral-500: #717680;
	--color-neutral-600: #535862;
	--color-neutral-700: #414651;
	--color-neutral-800: #252b37;
	--color-neutral-900: #181d27;
	--color-neutral-950: #0a0d12;

	--main-width: 1200px;
}

