html {
	font-size: 100%;
}

body {
	font: 400 100% 'PPNeueMontreal', sans-serif;
	color: var(--color-neutral-900);
}

h1,
h2,
h3,
h4,
h5,
h6 {
	display: inline-block;
	margin: 0;
	font-family: 'PPEditorialNew', sans-serif;
	color: var(--color-neutral-900);
}

button,
input,
select,
textarea {
	font: 400 1rem 'PPNeueMontreal', sans-serif;
	color: var(--color-neutral-900);
}

button:hover:not(:disabled) {
	cursor: pointer;
}
